import React, { useEffect } from 'react';
import { Button, Input } from 'reactstrap';

export const Rep = ({ item, OnItemChange }) => {
  useEffect(() => {}, []);

  return (
    <tr>
      <th scope="row">{item.id}</th>
      <td>
        {isEditable ? (
          <Input
            type="number"
            value={item.num}
            onChange={event => (item.num = event.target.value)}
          />
        ) : (
          _numReps
        )}
      </td>
      <td>
        {isEditable ? (
          <Input type="number" value={_weigth} onChange={event => setWeigth(event.target.value)} />
        ) : (
          _weigth
        )}
      </td>
      <td>
        <Button
          color={!isEditable ? 'primary' : 'danger'}
          onClick={!isEditable ? handleOnclickEdit : handleOnclickDone}
        >
          {!isEditable ? 'edit' : 'done'}
        </Button>
      </td>
    </tr>
  );
};
